

import "./Footer.css"
import { Link, useLocation } from "react-router-dom";
import React from "react";
import Logo from "../../images/logo.png"
import Facebook from "../../images/facebook.png"
import Twitter from "../../images/twitter.png"
import Instagram from "../../images/instagram.png"
const Footer = () => {
  const location = useLocation();
  return (
    <>
    {!location.pathname.includes("/form") && (
    <div className="footer">
{!location.pathname.includes("/blogpost") && (
        <div className="footer_top">
          <div className="footer_top_content">
            <div>
            <p className="text">Have a vision in mind?</p>
            <p className="small_text">
              Lets help you turn your vision into a finished product.
            </p>
            <Link href="/contact">
            <button className="btn orange_btn big_btn">
            Sell property now</button>
            </Link>
          </div>
          </div>
        </div>
          )}
      <div>
        <div >
        <div className="footer_bottom">
        <Link to="/">
                <img src={Logo} alt="" className="footer_logo" />
              </Link>
         
          <div className="pages">
            <p className="footer_title">Pages</p>
            <ul>
              <li>
                <a href="/#howItWorks">How it works</a>
              </li>
              <li>
                <a href="/#whoItWorksFor">Who is it for</a>
              </li>
            </ul>
          </div>
          <div className="contacts">
            <p className="footer_title">CONTACT INFORMATION</p>
            <p>ownerfi@gmail.com</p>
            <p>+244 584 5854</p>
            <p>No 33 Broadway close, Savannah Crescent US</p>
            <div className="social">
              <li>
                <Link href="/about-us">
                  <img src={Facebook} alt="facebook"/>
                </Link>
              </li>
              <li>
                <Link href="/about-us">
                  <img src={Twitter} alt="twitter"/>
                </Link>
              </li>
              <li>
                <Link href="/about-us">
                  <img src={Instagram} alt="instagram"/>
                </Link>
              </li>
              </div>
          </div>
        </div>
        </div>
      </div>
    </div>
    )}
    </>
  );
};

export default Footer;
